const cards = require('../../gatsby-config').siteMetadata.cards;

const ThankYouCardTemplate = ({ pageContext }) => {
  const { slug } = pageContext;
  // const cardData = cardDataLookup[slug];
  const cardData = cards[slug];


  // If there's valid card data, show the thank-you card content

    return (
      <div className="card-container">
        <div className="card">
          <div className="message">
            Dear {cardData.name},<br /> Thank you very much for {cardData.item}.
          </div>
        </div>
      </div>
    );
  }



export default ThankYouCardTemplate;
